import React, {Component} from 'react'
import Navbar from './component/Navbar'
import ProductListItem from './component/ProductListItem'
import Products from './component/Products'
import ProductDetail from './component/ProductDetail'
import SearchBar from './component/SearchBar'
import Cart from './component/Cart'
import PageAnimation from './component/PageAnimation'
import Footer from './component/Footer'
import _ from 'lodash'

import {getProducts} from './services/shop'
import { Redirect, Route, Switch } from 'react-router-dom'
import { CSSTransition } from 'react-transition-group'
import AppPicker from './component/AppPicker'

import {discountPrice, filterData, sortData} from './services/commons'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css';



export default class App extends Component {


    state = {
        allProducts: [],
        product: {},
        search: '',
        showFilterModal: false,
        filter: false,
        minPrice: 0,
        maxPrice: 0,
        isSelectedSort: '',
        isSelectedFilter: '',
        sort: false,
        sortValue: '',
        showSortModal: false,
        cart: [

        ]
    }
    
    componentDidMount = ()=> {

        this.setState({
            allProducts: getProducts()
        })

        console.log(window.location.href)
        
    }

    addToCart = (id)=> {
        const {allProducts} = this.state
        let product =  allProducts.find(product => product.id === id)
    let price = discountPrice(product.discount, product.price)
    product =  _.pick(product, ['id', 'name', 'discount', 'image', 'remaining' ])


    product.quantity = 1
    product.total = product.price
    product.price = price
    product.total = price

        this.setState({
            cart : [...this.state.cart, product]
        })
    }

    getProduct = (id)=> {
      const {allProducts} = this.state
        const product = allProducts.find(product => product.id == id)

        this.setState({
            product
        })

    }

    handleRemoveItem=(id)=> {
      
        this.setState({
            cart: this.state.cart.filter(item => item.id != id)
        })

    }

    handleIncrementItem = (id)=> {
      let cart = _.cloneDeep(this.state.cart)

      const index = cart.findIndex(item => item.id === id)

      cart[index].quantity += 1
      if(cart[index].quantity > cart[index].remaining) return false
      cart[index].total = cart[index].price * cart[index].quantity
       

      this.setState({
          cart
      })
       
    }
    handleDecrementItem = (id)=> {

        let cart = _.cloneDeep(this.state.cart)

        const index = cart.findIndex(item => item.id === id)
  
        cart[index].quantity -= 1
        if(cart[index].quantity < 1) return false
        cart[index].total = cart[index].price * cart[index].quantity
         
  
        this.setState({
            cart
        })
    }


    searchProducts =(search, products)=> {
          
      // return products.filter(product => product.name.toLowerCase().startsWith(search))

       let allProducts = []
       

       products.forEach(product => {
          
        let words = product.name.split(' ');

        words.forEach(word=> {
            if (word.toLowerCase().startsWith(search)){
                allProducts.push(product)
            }
        })

       })

       return allProducts;
    }    

    showFilterModal = ()=> {

        this.setState({
            showFilterModal: !this.state.showFilterModal
        })
    }

    showSortModal = ()=> {

        this.setState({
            showSortModal: !this.state.showSortModal
        })
    }

    filterProducts = (item)=> {
        
        this.setState({
            filter: true,
            minPrice: item.min,
            maxPrice: item.max,
            isSelectedFilter: item.label,
            showFilterModal: false
        })
        

    }

    sortProducts = (item) => {

        this.setState({
            isSelectedSort: item.label,
            sort: true,
            showSortModal: false
        })

    }

    
    updateSearch = (e)=> {
        this.setState({
            search: e.target.value
        })
      }

   

    render() {
        let {allProducts, cart, filter, sort,  minPrice, maxPrice,  product, search, showFilterModal, showSortModal ,isSelectedSort, isSelectedFilter} = this.state

       

        if(filter){
            allProducts = allProducts.filter(product => product.price > minPrice && product.price < maxPrice)
        }

        if(sort){
           let  type = isSelectedSort === 'Highest to Lowest Price'? 'desc' : 'asc'
            allProducts = _.orderBy(allProducts, 'price', type)
            console.log(allProducts)
        }

        if(search){
            allProducts = this.searchProducts(search, this.state.allProducts)
        }

      

        return (<div>
            <ToastContainer />
            <Navbar cart={cart}/>
            {window.location.href.endsWith('/') && <SearchBar search={search}  updateSearch={this.updateSearch}/> }  
             <Switch>
                   <Route exact path="/shopping-cart/products/:id" render = {(props)=> (
                       <PageAnimation>

                           <ProductDetail getProduct={this.getProduct} product={product} {...props} addToCart={this.addToCart} cart={cart}/>
                       </PageAnimation>
                   )} />

                  <Route  exact path="/shopping-cart" render={props => (
                      <Products count={allProducts.length} showFilterModal={this.showFilterModal} products={allProducts} {...props} showSortModal={this.showSortModal} />
                  )}  />

                  <Route  exact path="/shopping-cart/cart" render={props => (
                        <Cart handleRemoveItem={this.handleRemoveItem} cart={cart} handleIncrementItem={this.handleIncrementItem} handleDecrementItem = {this.handleDecrementItem}/>
                     )}  />
                  

             </Switch>



           <CSSTransition in={showFilterModal} classNames="MODAL" mountOnEnter={true} unmountOnExit={true} timeout={{enter: 1000, exit: 500}} >
               <AppPicker onSelect={this.filterProducts} data={filterData} onCloseModal={this.showFilterModal} isSelected={isSelectedFilter} title="FILTER BY PRICE"/>
           </CSSTransition>

           <CSSTransition in={showSortModal} classNames="MODAL" mountOnEnter={true} unmountOnExit={true} timeout={{enter: 1000, exit: 500}} >
               <AppPicker onSelect={this.sortProducts} data={sortData} onCloseModal={this.showSortModal} isSelected={isSelectedSort} title="SORT"/>
           </CSSTransition>
            
            
            
            <Footer/>
           
            {/* <Cart/> */}
           

            
        </div>)
    }
}



