import React, { Component } from 'react'
import ProductList from './ProductList'

export default class Products extends Component {


    render() {
        const {products, showFilterModal, showSortModal, count } = this.props
        if(products.length < 1) return (<div className="text-center lead mt-5 font-weight-bold appCard p-5 "> Nothing to Show</div>)
        return (
            <div className="products">
                <div className="appCard">
             <div  className="searchDetails">
                   
                       <h3> Search</h3>
                       <p> {count} Products </p>
                    </div>
                </div>

                <div className="appCard">
                <div className="filter_sort">
                        <button className="filter"  onClick={showFilterModal}><i className="fa fa-filter"></i> Filter</button>
                        <div className="divider">|</div>
                        <button className="sort" onClick={showSortModal}><i className="fa fa-sort"></i> Sort</button>
                    </div>
                </div>

                <ProductList  products={products}/>
            </div>
        )
    }
}



