import React, { Fragment } from 'react'
import _ from 'lodash'
import {Link} from 'react-router-dom'

import {discountPrice} from '../services/commons'
const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'NGN',
    minimumFractionDigits: 0
  })



function ProductListItem({product}) {
    const {id, discount, image, name, price, rating} = product
    let discountedPrice
    if(discount)   discountedPrice = discountPrice(discount, price)
   
      


    return (
        <Link to={`/shopping-cart/products/${id}`}>
        <div className="appCard">
            <div className="productItem" >
        
            <img src={image} alt="stuff" />
           {discount && <span> {discount}% OFF</span>} 
            <div className="productItem__detail">
               <p className="productItem__name">{name}</p>
                
                <div className="productItem__price">
               {
               discount ? 
               <Fragment>
               <p className="productItem__discounted">{formatter.format(price)}</p>
               <s className="productItem__full">{formatter.format(price)}</s>
               </Fragment> :
               
               <p className="productItem__discounted">{formatter.format(price)}</p> 
                }      
             
                </div> 
               
               <p className="productItem__seller">sold by dr davik </p>

                <div className="productItem__rating">
                   
                     {_.range(rating).map(num=> (
                          <i key={num} className="fa fa-star"/>
                     ))
                   
                     }

                     {
                         rating < 5 ? _.range(5-rating).map(num=> (
                            <i  key={num} className="fa fa-star-o"/>
                         )) : ''
                     }
                </div>

                 
            </div>

            </div>
        </div>
      
        </Link>
    )
}

export default ProductListItem



