import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { discountPrice } from '../services/commons'

const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'NGN',
    minimumFractionDigits: 0
  })

export default class ProductDetail extends Component {

     state = {
         isAddedToCart : false
     }
    
    
    componentDidMount = ()=> {
       const {match, getProduct, cart} = this.props
       getProduct(match.params.id)
       

       if(cart.find(item => item.id == match.params.id )) {

        this.setState({
            isAddedToCart: true
        })
       }
    
    }

    handleAddToCart = (id)=> {

        const {isAddedToCart} = this.state
       
        const {addToCart} = this.props

        this.setState({
            isAddedToCart: true
        })
        addToCart(id)
        
    }

    render() {
       const {addToCart} = this.props
       
      
        
        if(!this.props.product) return 'loading'

        let {id, description, image, name, price,  discount} = this.props.product
        const {isAddedToCart} = this.state
        if(discount){
            price = discountPrice(discount, price)
        }

        return (
            <div className="productDetail">
                <img  src={image} alt="controller"/>

                <div className="productDetail__name">
                    <h3>{name}</h3>
                    <p> Product Code: <span>{id}</span></p>
                </div>

                <div className="productDetail__price">
                {formatter.format(price)}
                </div>

                <div className="productDetail__description">
                    <h3>Description:</h3>
                     <p>{description}
                  </p> </div>
        { !isAddedToCart && <button className="appButton appBg-success" onClick={()=> this.handleAddToCart(id)} >Add To Cart</button> }

        { isAddedToCart && <Link to="/shopping-cart/cart"><button className="appButton appBg-success"  >Go To Cart</button> </Link>}
            </div>
        )
    }
}



