import React from 'react'
import { Link } from 'react-router-dom'

function Navbar({cart}) {
    return (
        <div className="appNavbar">
            {/* <div className="appNavbar__hamburger"></div> */}

            <Link to="/shopping-cart" className="appNavbar__logo">
                My Shop
            </Link>

            <div className="appNavbar__notification">
                <Link to="/shopping-cart/cart"><i className="fa fa-shopping-cart"></i> </Link>
       <span>{cart.length > 0 && cart.length}</span>
            </div>      
        </div>
    )
}

export default Navbar



