import React from 'react'
import {CSSTransition} from "react-transition-group";

function PageAnimation(props) {
   
    return (
        <CSSTransition in={true}
        
        timeout={
            {
                enter: 500,
                exit: 500
            }
        }
        mountOnEnter={true}
        appear={true}
        unmountOnExit={true}
        classNames="PAGE">

            {props.children}

</CSSTransition>
    )
}

export default PageAnimation



