import React from 'react'
import {CSSTransition} from 'react-transition-group'

function AppPicker({data, onSelect, onCloseModal, isSelected, title}) {
    return (
       
     
        <div className="appModal"> 
        <div className="appModalCloseButton" onClick={()=> onCloseModal()}>Close</div>
                 <div className="filter">
                    
                     <ul className="filter__list">
                     <h3 style={{color: 'grey', textAlign:"center"}}> {title}</h3>
                        {data.map(item=> (
                            <li key={item.label} className="filter__listItem"> <input checked={item.label == isSelected && 'true'} onClick={()=> onSelect(item)}  type="checkbox"/>  {item.label}</li>
                        ))}
                         
                       
                     </ul>
                    
                 </div>
      </div>
     
    
    )
}

export default AppPicker



