import React from 'react'
import { withRouter } from "react-router-dom";

function SearchBar({updateSearch, search}) {
    return (
            <div style={{padding: "1rem", backgroundColor: 'white'}} >

                <input onChange={updateSearch} value={search} className="searchBar" type="text" placeholder="Search for a products" />
          
            </div>
    )
}

export default withRouter(SearchBar)



