import React, {useState} from 'react'
import { Link } from 'react-router-dom'
import { discountPrice } from '../services/commons'
import {toast} from 'react-toastify'

const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'NGN',
    minimumFractionDigits: 0
  })

function Cart({cart, handleRemoveItem, handleIncrementItem, handleDecrementItem}) {
     
    const [error, setError] = useState(['', true])

    let total = 0
     cart.forEach(({total:price})=> {
         total +=  price
         
     })

     const IncrementQuantity = (id)=> {
        
        
      const result = handleIncrementItem(id)

      if(result == false) {
        setError([id, true])
      }

     }
     const decrementQuantity = (id)=> {
        
        
      const result = handleDecrementItem(id)
   

        setError(false)
      
     }

     const handleCheckout = ()=> {

        toast('Sorry Not taking Orders at the Moment ')
     }

     
   if(cart.length < 1) {

    return <div className="appCard lead text-center mt-4 lead font-weight-bold text-capitalize py-4"> You have no items in your cart</div>
   }
    return (
        <div className="cart">
            <div className="cart__title">Shopping Cart</div>
            <div className="cart__total">
                <h3>Subtotal (2 Items)</h3>
                 <p>{formatter.format(total)}</p>
            </div>

            <Link to={`/shopping-cart`}>
            <button className="continueShopping appBg-danger">
               <span>&larr;</span>  Continue Shopping
            </button>
            </Link>
             
             <div className="cart__list">

                 {cart.map(({id, image,name, price,quantity, total, amount}) => (

                    <div key={id} className="cart__listItem">
                    <span onClick={()=> handleRemoveItem(id)}>X</span>
                    <div  className="cart__info">
                    <img src={image} alt="controller"/>
                    <div className="cart__detail">
                    <h3>{name}</h3>
                    <p>Sold by <span>Dr Davik</span></p>

                    </div>
                    </div>

                    <div className="cart__quantity"> 
                    <p className="quantity">Quantity:</p>
                    <div className="cart__buttons">
                    <button className="appsquareButton " onClick={()=> decrementQuantity(id) } >-</button>  
                 <button  className="appsquareButton">{quantity}</button>
                    <button className="appsquareButton " onClick={()=> IncrementQuantity(id)} >+</button>  
                    </div>
                    { (error[0] == id && error[1] === true) && <div className="error" style={{marginTop: '4rem', position: 'absolute', bottom: '-1rem'}}> Only  {quantity}  remaining in stock</div>}
                 <p className="price">{formatter.format(total)}</p>

                        <p className="totalPrice">{formatter.format(price)} * {quantity} item (s)</p>
                    

                    
                    </div>
                    
                    </div>
                                    ))}

            <button onClick={()=> handleCheckout()} className="appButton appBg-success">Proceed To Checkout</button>
               
                
            

            
        </div>
        </div>
    )
}

export default Cart



